import { projects, type Project } from "../assets/projects/project";

import "@/styles/project.css";

function Project() {
  return (
    <div>
      {
        projects.map((project: Project, id: number) => {
          if(project.link.trim() !== "") {
            return (
              <a href={project.link} target="_blank" className="project-card-link">
                <div className="project-card">
                  <div className="project-header">
                    <h4>{project.title}</h4>
                  </div>
                  <div className="project-metadata">
                    <span>{project.type}</span>
                    <span>{project.category}</span>
                  </div>
                  <div className="project-description">
                    <p>{project.description}</p>
                  </div>
                  <div className="tech-tags">
                    {
                      project.tools.map((tool: string, tech_id: number) => (
                        <span>
                          { tool }
                        </span>
                      ))
                    }
                  </div>
                </div>
              </a>
            )
          } else {
            return (
              <div className="project-card">
                <div className="project-header">
                  <h4>{project.title}</h4>
                </div>
                <div className="project-metadata">
                  <span>{project.type}</span>
                  <span>{project.category}</span>
                </div>
                <div className="project-description">
                  <p>{project.description}</p>
                </div>
                <div className="tech-tags">
                  {
                    project.tools.map((tool: string, tech_id: number) => (
                      <span>
                        { tool }
                      </span>
                    ))
                  }
                </div>
              </div>
            )
          }
        })
      }
    </div>
  )
};


export default Project;

