import aboutText from "../assets/text/about.txt";

function Overview() {
  const about = aboutText.trim();
  return (
    <div>
      <h3>
        {
          about.split("\n").map((line: string, index: number) => (
            <span key={index}>
              { line }
              <br />
            </span>
          ))
        }
      </h3>
    </div>
  )
};

export default Overview;
